import React, { forwardRef, HTMLAttributes, PropsWithChildren } from "react";
import styled, { css } from "styled-components";

import IconButton from "../Button/IconButton";
import { colors, spacingMap } from "../styleguide";

import { StyledModalProps } from "./Modal.types";
import { useModalContext } from "./ModalContext";

interface ModalHeaderBaseProps extends HTMLAttributes<HTMLHeadElement>, Pick<StyledModalProps, "variant"> {
	dataQA: string
	showCloseButton?: boolean
	className?: string
}

export type ModalHeaderProps = PropsWithChildren<ModalHeaderBaseProps>;

const border = css`
	border-bottom: 1px solid ${colors.lightGray};
`;

const buttonPosition = css`
	position: absolute;
	right: 10px;
	top: 10px;
`;

const HeaderWrapper = styled.div<Omit<ModalHeaderProps, "dataQA">>`
	display: inline-flex;
	align-items: center;
	justify-content: ${({ variant }) => variant?.includes("graphic") && "center"};
	padding: ${spacingMap.md} ${spacingMap.md} ${spacingMap.sm};
	// Divider for scrollable variant is just a bottom border on the HeaderWrapper
	${({ variant }) => variant === "scrollable" && border};
`;

const CloseButtonDark = styled(IconButton)`
	${buttonPosition};
	background: ${colors.black};
	color: ${colors.white};
`;

const CloseButton = styled(IconButton)`
	${buttonPosition};
`;

const ModalHeader = forwardRef<HTMLDivElement, ModalHeaderProps>(({
	children,
	dataQA,
	showCloseButton = true,
	className,
}, ref) => {
	const { onRequestClose, variant } = useModalContext();
	return (
		<HeaderWrapper
			ref={ref}
			variant={variant}
			data-qa={`${dataQA}-modal-header`}
			className={className}
		>
			{children}
			{showCloseButton && (
				variant?.includes("graphic")
					? (
						<CloseButtonDark
							icon="CircleX"
							dataQA={`${dataQA}-modal-close-button-dark`}
							aria-label="close modal"
							variant="tertiary"
							onClick={onRequestClose}
						/>
					)
					: (
						<CloseButton
							icon="CircleX"
							dataQA={`${dataQA}-modal-close-button`}
							aria-label="close modal"
							variant="tertiary"
							onClick={onRequestClose}
						/>
					)
			)
			}
		</HeaderWrapper>
	);
});

ModalHeader.displayName = "ModalHeader";
export default ModalHeader;
